*{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
}

#root{
  background-color: rgb(36, 35, 46);
  background-size:auto;
  color: white;
}

body{
  background-color: rgb(36, 35, 46) !important;
}

body::-webkit-scrollbar{
  display: none;
}

.bottom-nav {
  width: 95%;
  height: 70px;
  justify-content: space-evenly;
  align-items: center;
  align-self: center;
  position: fixed;
  bottom: 10px;
  margin: 5% auto;
  left: 0;
  right: 0;
  background-color: rgb(53, 52, 68);
  color: rgb(53, 52, 68);
  border-radius: 10px;
  filter: drop-shadow(0 0 0.80rem rgb(0, 0, 0));
}
.center{
  display: flex;
  padding-top: 50%;
  align-items: center;
  justify-content: center;
}
.nav-link {
  color: rgb(255, 255, 255) !important;
}

.nav-link:active, .nav-link:focus, .nav-link:hover {
  color: rgba(255, 106, 146, 0.76) !important;
}

.exercise-add-button{
  border-radius: 50%;
  width: 70px;
  height: 70px;
  border: none;
  background-color: rgba(36, 131, 255, 0.76);
  color: white;
  filter: drop-shadow(0 0 0.80rem rgb(0, 0, 0));
  position: fixed;
  bottom: 0;
  right: 0;
  margin-bottom: 100px;
  margin-right: 20px;
  transition-duration: 0.5s;
}

.workout-add-button{
  border-radius: 50%;
  width: 70px;
  height: 70px;
  border: none;
  background-color: rgba(255, 36, 171, 0.76);
  color: white;
  filter: drop-shadow(0 0 0.80rem rgb(0, 0, 0));
  position: fixed;
  bottom: 0;
  right: 0;
  margin-bottom: 200px;
  margin-right: 20px;
  transition-duration: 0.5s;
}
h1{
  margin: 0 !important;
}
.exercise-add-button:hover{
  background-color: rgba(79, 153, 250, 0.76);
}
.workout-add-button:hover{
  background-color: rgba(190, 22, 126, 0.76);
}

.exercise-modal{
  background-color: rgb(53, 52, 68);
  filter: drop-shadow(0 0 0.20rem rgb(0, 0, 0));
  border-radius: 10px;
  height: 100%;
  margin-top: 20px;
  padding-top: 20px;
}

.input-element {
  margin: 25px;
  margin-top: 25px !important;
  padding: 10px;
  border: none;
  filter: drop-shadow(0 0 0.20rem rgb(0, 0, 0));
}

.add-button{
  border: none;
  width: 20% !important;
  border-radius: 10px;
  background-color: rgba(36, 131, 255, 0.76);
  color: white;
  margin-left: 25px;
  margin-bottom: 25px;
  padding: 15px;
}

.add-button:hover{
  background-color: rgba(79, 153, 250, 0.76);
}

.object-box{
  background-color: rgb(53, 52, 68);
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  height: auto;
  margin-top: 20px !important;
  padding-top: 20px;
}
.setting-buttons{
  background-color: rgb(53, 52, 68);
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
  display: flex;
  flex-direction: column;
  text-align: center;
  border-radius: 10px;
  height: auto;
  margin-top: 20px;
  padding: 10px;
}
.user-info{
  background-color: rgb(53, 52, 68);
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  height: 50vh;
  margin-top: 20px;
  padding: 20px;
  width: 100%;
  justify-content: space-around;
}
.user-info-row{
  display: flex;
  justify-content: space-between;
}

.setup-btn{
  border: none;
  background-color: rgb(53, 52, 68);
  color: gray;
}

.btn-active{
  color: white;
}

.center-content{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 20px;
}
.exerciseOption {
  border-radius: 10px;
  border: rgb(255, 106, 146) 2px solid;
  height: 300px !important;
  overflow-y: auto;
  padding-bottom: 15px;
}
.exerciseOption::-webkit-scrollbar {
  display: none;
}

.selected {
  background-color: rgb(0, 184, 108) !important;
}

.workout-info{
  background-color: rgb(53, 52, 68);
  flex-direction: column;
  text-align: start;
  padding-left: 5%;
}

.remove-button {
  border: hidden;
  background: none;
  font-weight: bold;
  font-size: large;
  color: rgb(182, 182, 182);
  text-align: end;
  padding-right: 5%;
}

.content{
  padding-bottom: 90px !important;
}

.pointer-hover{
  cursor: pointer;
}

.home-objects{
  background-color: rgb(53, 52, 68);
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  margin: 10px;
  height: 25vh;
  width: 40vw;
  justify-content: center;
  cursor: pointer;
}

.containers {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.banner{
  padding-top: 10vh;
  padding-bottom: 5vh;
}

.home-recent{
  margin: 10px;
  text-align: left;
  width: 100%;
}

.timer-btns{
  display: flex;
  padding: 10px;
  justify-content: space-evenly;
}

.play{
  padding:20px;
  border: rgb(255, 106, 146) 3px solid;
  border-radius: 50%;
  height: 70px;
  width: 70px;
  cursor: pointer;
}
.play:hover{
  background-color: rgb(255, 106, 146);
}

.pause{
  padding:20px;
  border: rgb(0, 184, 108) 3px solid;
  border-radius: 50%;
  height: 70px;
  width: 70px;
  cursor: pointer;
}
.pause:hover{
  background-color: rgb(0, 184, 108);
}

.timer{
  font-size: 75px;
  padding: 20%;
}

.exercise{
  background-color: rgb(184, 0, 25);
}

.rest{
  background-color: rgb(243, 201, 84);
}

.done-status{
  background-color: rgb(0, 184, 108);
}

.news-input{
  height: 100%;
}

input{
  width: 90% !important;
  padding: 10px;
  background-color: rgb(53, 52, 68);
  border: none;
  color: white;
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
  text-align: center;
  border-radius: 20px;
}

input:focus{
  outline: none;
}
input::placeholder{
  color: white;
}
.align-center{
  align-self: center;
}

.select-input{
  background-color: rgb(53, 52, 68);
  color: white;
  border: none;
}

.submit-btn{
  padding: 15px;
  background-color: rgb(0, 184, 108);
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
  border: none;
  border-radius: 10px;
  color: white;
}

.kcal-circle{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: gray 3px solid;
  border-radius: 50%;
  width: 150px;
  height: 150px;
}

.nutrition-info{
  display: flex;
  flex-direction: row;
  padding-top: 20px;
  width: 100%;
  align-content:space-between;
  justify-content: space-evenly;
}
.macros{
  font-weight: lighter;
  margin: 0;
}
.daily-intake-box{
  display: flex;
  align-items: baseline;
  background-color: rgb(53, 52, 68);
  justify-content: space-between;
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
  border: gray 0.5px solid;
  border-radius: 10px;
  padding: 20px;
  width: 100%;
  text-align: left;
}
.icon-padding{
  margin-right: 10px;
}
.food-item{
  width: 100%;
}
.kcal-info{
  font-weight: lighter;
}
.nutrition-table{
  padding:15px;
}
.margin-bottom {
  margin-bottom: 25px;
  height: 100%;
  flex-wrap: nowrap !important;
  padding-top: 20px;
}
.post{
  display: flex;
  flex-direction: row;
}
.language{
  margin-top: 20px;
  padding: 10px;
  border-radius: 10px;
  filter: drop-shadow(0 0 0.3rem rgb(0, 0, 0));
}
.full{
  width: 100% !important;
  margin-top: 20px;
}
.friends{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.btn{
  height: 100%;
}
.input-center{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.recent{
  display: flex;
  padding-left: 20px;
}